import React from 'react';
import { Box } from '@mui/system';
import Paper from '@mui/material/Paper';
import Grid from '@mui/material/Unstable_Grid2';
import { styled } from '@mui/material/styles';
import { Typography } from '@mui/material';

const Item = styled(Paper)(({ theme }) => ({
  backgroundColor: theme.palette.mode === 'dark' ? '#1A2027' : '#fff',
  ...theme.typography.body2,
  padding: theme.spacing(1),
  textAlign: 'center',
  color: theme.palette.text.secondary,
}));

const Aboutme = () => {
  return (
    <div>
      <Grid container spacing={{ xs: 2, md: 3 }} columns={{ xs: 4, sm: 8, md: 12 }}>
        <Grid xs={4}>
          <Item><img src='./profilepic1.jpeg' alt='profile pic' height='300px' style={{borderRadius:"50%"}}/></Item>
        </Grid>
        <Grid xs={8}>
          <Item>
            <Typography fontFamily={'monospace'} fontSize={12}
          sx={{lineHeight:'4.5',paddingLeft:'20px', paddingTop:'40px'}}>
              Hello, I am Sharang Ahirekar, I am a full stack web developer in MERN stack.<br/>
              I help people realise their website design and I strive to make them close to their expectations.<br/>
              I read about technology and wonder where it could lead us!<br/>
              There is an amazing book co-authored by Peter Diamandis titled 'The Future is Faster than you think' talks <br/> 
              about fast evolving technologies. A must read for every tech enthusiast!

            </Typography>
          </Item>
        </Grid>
        <Grid>
          <Item>
            <Typography variant='h4' component="h4">Reach Me</Typography>
            <Typography>
              <b>Email</b>: sharangahirekar1@gmail.com
            </Typography>
            <Typography>
              <b>Phone No.</b>: +91 98885 39637
            </Typography>
          </Item>
        </Grid>
      </Grid>
    </div>
  )
}

export default Aboutme