import React, { useContext } from 'react';
import {
  Box,
  Drawer,
  Button,
  List,
  Divider,
  ListItem,
  ListItemButton,
  ListItemIcon,
  ListItemText
} from '@mui/material';
import HomeIcon from '@mui/icons-material/Home';
import PersonAddIcon from '@mui/icons-material/PersonAdd';
import AccountTreeIcon from '@mui/icons-material/AccountTree';
import SchoolIcon from '@mui/icons-material/School';
import {useNavigate} from 'react-router-dom';
import { DrawerContext } from '../contexts/DrawerContext';

const nav = [
  {page:"Home",icon:<HomeIcon/>,route:'./'},
  {page:"About me",icon:<PersonAddIcon/>,route:'./aboutme'},
  {page:"Projects",icon:<AccountTreeIcon/>,route:'./projects'},
  {page:"Education",icon:<SchoolIcon/>,route:'./education'}
]

const DrawerComponent = ({anchor='left'}) => {
  const {toggleDrawer, isOpen} = useContext(DrawerContext)
  const navigate = useNavigate();

  return (
    <div style={{marginTop:'70px'}}>
          <Drawer 
            anchor={anchor}
            open={isOpen}
            onClose={toggleDrawer(false)}
          >
            <Box>
              <List>
                {nav.map((p)=>(
                  <ListItem key={p.page} onClick={()=>navigate(`/${p.route}`)}>
                    <listItemButton sx={{display:'flex'}} onClick={toggleDrawer(false)}>
                      <ListItemIcon>
                        {p.icon}
                      </ListItemIcon>
                      <ListItemText>{p.page}</ListItemText>
                    </listItemButton>
                  </ListItem>
                ))}
              </List>
            </Box>
          </Drawer>
    </div>
  )
}

export default DrawerComponent;