import React from 'react';
import CardMaterial from '../components/CardMaterial';
import Grid from '@mui/material/Unstable_Grid2';

const projects = [
  {heading:"SSense Clone",content:"A Team project, cloning Ssense - ecommerce site using Vanilla JS",img:"./ssense-clone.png",github:"https://github.com/surajDongre-16/Ssense-clone",deploy:"https://ssense-6040cb.netlify.app/"},
  {heading:"GeoAddress Component",content:"Allow the location, and get your address and location on map",img:"./geoaddress-component.png",github:"https://github.com/sharangahirekar1/geoaddress-component",deploy:"https://geoaddresscomponent.vercel.app/"},
  {heading:"Todo Full Stack",content:"A Full stack todo application, which can add, delete, update todos",img:"./todo-full-stack.png",github:"https://github.com/sharangahirekar1/todo-full-stack",deploy:"https://todo-front-topaz.vercel.app/"}
]
const Projects = () => {
  return (
    <Grid container spacing={{ xs: 2, md: 3 }} columns={{ xs: 4, sm: 8, md: 12 }}>
      {projects.map((p)=>{
        return  <Grid><CardMaterial {...p} /></Grid>
      })}
    </Grid>
  )
}

export default Projects