import { Button, TextField } from '@mui/material';
import React, { useState } from 'react';
import axios from 'axios';

const ContactForm = ()=>{
    const [contact,setContact] = useState({});
    const handleChange = (e)=>{
        const {name,value} = e.target;
        setContact({
            ...contact,[name]:value
        })
    }
    
    const handleSubmit = ()=>{
        axios({
            method:'POST',
            url:'https://backend-server-ng1g.onrender.com/contact',
            data: JSON.stringify(contact),
            headers:{
                'Content-Type': 'application/json'
            }
        }).then((res)=>{
            alert("Thank you, I am glad I can connect with you");
        })
    }
    return (
        <div>
            <TextField sx={{mb:'15px'}} name='name' label='Name' onChange={handleChange}/>
            <TextField sx={{mb:'15px'}} name='email' label='Email'onChange={handleChange}/>
            <TextField sx={{mb:'15px'}} name='mobile' label='Phone No.' onChange={handleChange}/>
            <TextField sx={{mb:'15px'}} name='message' label='Message' multiline row={4} onChange={handleChange}/>
            <br/>
            <Button variant='contained' onClick={handleSubmit}>Contact Me</Button>
        </div>
    )
}

export default ContactForm;