import React, { useContext } from 'react';
import {
  AppBar,
  Box,
  Toolbar,
  IconButton,
  Typography,
  Menu,
  Container,
  Button,
  Tooltip,
  MenuItem,
  Avatar,
  Fab
} from '@mui/material';
import MenuIcon from '@mui/icons-material/Menu';
import SelfImprovementIcon from '@mui/icons-material/SelfImprovement';
import { DrawerContext } from '../contexts/DrawerContext';
import { useNavigate }  from 'react-router-dom';

const pages = ['Home','Resume','Github','LinkedIn'];

const Appbar = () => {
  const [anchorElNav, setAnchorElNav] = React.useState(null);
  const [anchorElUser, setAnchorElUser] = React.useState(null);
  const navigate = useNavigate();

  const handleOpenUserMenu = (event) => {
    setAnchorElUser(event.currentTarget);
  };

  const handleCloseUserMenu = () => {
    setAnchorElUser(null);
  };

  const handleNavigation = (page)=>{
    if(page === "Home") navigate("/");
    else navigate(`/${page}`);
  }

  const {toggleDrawer} = useContext(DrawerContext);

  return (
    <div>
      <AppBar>
        <Container>
          <Toolbar>
            <Fab onClick={toggleDrawer(true)} sx={{display:{xs:'none',md:'flex'},mr:0,cursor:'pointer'}} color="primary"><SelfImprovementIcon /></Fab>
            <Typography
              sx={{
                mr:2,
                ml:2,
                display:{xs:'none',md:'flex'},
                fontFamily: 'monospace',
                fontWeight:700,
                letterSpacing:'.3rem',
                color:'inherit',
                textDecoration:'none'
              }}
            >SHARANG</Typography>
            <Box sx={{flexGrow:1, display:{xs:'flex',md:'none'}}}>
              <IconButton 
                size='large'
                color='inherit'
                >
                <MenuIcon onClick={toggleDrawer(true)}/>
              </IconButton>
              <Menu
                sx={{
                display:{xs:'block',md:'none'}
              }}>
                {pages.map((p)=><MenuItem key={p}>
                  <Typography>{p}</Typography>
                </MenuItem>)}
              </Menu>
            </Box>
            <SelfImprovementIcon sx={{display:{xs:'flex',md:'none'},mr:0,cursor:'pointer'}} onClick={toggleDrawer(true)} />
            <Typography
              sx={{
                mr:2,
                display:{xs:'flex',md:'none'},
                flexGrow:1,
                fontFamily:'monospace',
                fontWeight:700,
                letterSpacing:'.3rem',
                color:'inherit',
                textDecoration:'none'
              }} 
            >SHARANG</Typography>
            <Box sx={{flexGrow:1,display:{xs:'none',md:'flex'}}}>
              <Button onClick={()=>navigate("/")} sx={{my:2,color:'white',display:'block'}}>Home</Button>
              <Button onClick={()=>window.open("https://drive.google.com/file/d/11d4qtrJZ4PIGI3j8nr0ab0DaOjZ8fCYk/view?usp=sharing",'_blank' )} sx={{my:2,color:'white',display:'block'}}>Resume</Button>
              <Button onClick={()=>window.open("https://github.com/sharangahirekar1",'_blank' )} sx={{my:2,color:'white',display:'block'}}>Github</Button>
              <Button onClick={()=>window.open("https://www.linkedin.com/in/sharangahirekar1/",'_blank' )} sx={{my:2,color:'white',display:'block'}}>LinkedIn</Button>
            </Box>
            <Box sx={{flexGrow:0}}>
              <Tooltip title='Sharang Ahirekar'>
                <Fab color='primary' size='medium' onClick={handleOpenUserMenu} sx={{p:0}}>
                  <Avatar src="./profilepic.jpeg"/>
                </Fab>
              </Tooltip>
              <Menu
              sx={{ mt: '45px' }}
              id="menu-appbar"
              anchorEl={anchorElUser}
              anchorOrigin={{
                vertical: 'top',
                horizontal: 'right',
              }}
              keepMounted
              transformOrigin={{
                vertical: 'top',
                horizontal: 'right',
              }}
              open={Boolean(anchorElUser)}
              onClose={handleCloseUserMenu}
            >

                <MenuItem onClick={handleCloseUserMenu}>
                  <Typography textAlign="center" onClick={()=>navigate('/')}>Home</Typography>
                </MenuItem>
                <MenuItem onClick={handleCloseUserMenu}>
                  <Typography textAlign="center" ><a style={{textDecoration:'none',color:"black"}} target="_blank" href="https://drive.google.com/file/d/11d4qtrJZ4PIGI3j8nr0ab0DaOjZ8fCYk/view?usp=sharing" download='Sharang-Ahirekar-Resume'>Resume</a></Typography>
                </MenuItem>
                <MenuItem onClick={handleCloseUserMenu}>
                  <Typography textAlign="center" ><a style={{textDecoration:'none',color:"black"}} target="_blank" href="https://github.com/sharangahirekar1">Github</a></Typography>
                </MenuItem>
                <MenuItem onClick={handleCloseUserMenu}>
                  <Typography textAlign="center"><a style={{textDecoration:'none',color:"black"}} target="_blank" href="https://www.linkedin.com/in/sharangahirekar1/">LinkedIn</a></Typography>
                </MenuItem>

            </Menu>
            </Box>
          </Toolbar>
        </Container>
      </AppBar>
    </div>
  )
}

export default Appbar