import React from 'react';
import {Timeline, TimelineEvent} from 'react-event-timeline';

const Education = () => {
  return (
    <div>
      <Timeline>
        <TimelineEvent
          title=""
          createdAt="Jan 2022 - Present" 
          container="card"
          cardHeaderStyle={{
            backgroundColor:'#7DCE13',
            color:'#fff'
          }}
        ><b>Masai School</b><br/>Student - Web development [MERN]</TimelineEvent>
        <TimelineEvent
          title=""
          createdAt="Aug 2017 - Nov 2021"
          container="card"
          cardHeaderStyle={{
            backgroundColor:'#D75281',
            color:'#fff'
          }}
        ><b>Lovely Professional University</b><br/>B.tech Graduate</TimelineEvent>
        <TimelineEvent
          title=""
          createdAt="Apr 2016 - May 2017"
          container="card"
          cardHeaderStyle={{
            backgroundColor:'#0096FF',
            color:'#fff'
          }}
        ><b>St Ann's Junior College</b><br/>HSC State Board</TimelineEvent>
        <TimelineEvent
          title=""
          createdAt="Apr 2014 - May 2015"
          container="card"
          cardHeaderStyle={{
            backgroundColor:'#395B64',
            color:'#fff'
          }}
        ><b>St Ann's Convent School</b><br/>SSC State Board</TimelineEvent>
      </Timeline>
    </div>
  )
}

export default Education